//成本中心
.cost {
  position: relative;
  padding: 10px;
  &-card {
    @extend %card;
    margin-bottom: 10px;
    .flex-row-3 {
      padding-left: 5px;
    }
    .mess-txt {
      position: relative;
      font-size: 15px;
      color: $font-color-gray;
      padding-right: 18px;
      &::after {
        content: '';
        position: absolute;
        top: 6px;
        right: 3px;
        @include arrow('right', 8px, 8px, #8e8e8e);
      }
      .state {
        float: right;
        font-size: 12px;
        line-height: 12px;
        color: #ffffff;
        padding: 6px 8px;
        border-radius: 4px;
        &-on {
          background: #5ebf44;
        }
        &-off {
          background: #ccc;
        }
      }
    }
  }

  &.cost-no {
    text-align: center;
    padding: 70px 0;
    .cost-no-data {
      color: #999;
      line-height: 18px;
      margin-top: 10px;
    }
  }
  .cost-card {
    position: relative;
    padding: 0;
    &:last-child{
      margin-bottom: 0;
    }
    .c-wrapper {
      padding: 10px;
    }
    .icon-bin {
      position: absolute;
      top: 10px;
      right: 10px;
    }
  }
  .operate-box {
    position: relative;
    padding: 8px 10px;
    @include clearfix;
    &::before {
      content: '';
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 1px;
      background: #e8e8e8;
      transform: scaleY(0.5);
      position: absolute;
    }
    .btn {
      float: right;
      display: inline-block;
      padding: 7px 15px;
      font-size: 12px;
      line-height: 12px;
      border-radius: 3px;
      text-align: center;
    }
    .btn-assit {
      color: #333333;
      border: 1px solid #e8e8e8;
    }
    .btn-main {
      color: #4175fe;
      border: 1px solid #4175fe;
      margin-left: 10px;
    }
    .btn-active {
      padding: 7px 10px;
      color: #ffffff;
      border: 1px solid transparent;
      background: #4175fe
    }
    .btn-disabled {
      padding: 7px 10px;
      color: #ccc;
      border: 1px solid #ccc;
      background: #fff
    }
  }
  .flex-row {
    .txt-ellipsis {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
  }
}

.tip-grey {
  color: $font-color-light;
  font-size: 12px;
  line-height: 18px;
  padding: 0 10px 20px 10px;
}