// APP绑定
.bind-phone {
	padding: 0 15px;

	.bind-result {
		padding: 70px 0 30px;
		text-align: center;
		border-bottom: 1px solid #ccc;

		h3 {
			margin: 5px 0;
			font-size: 16px;
		}

		p {
			font-size: 12px;
			color: #999;
		}
	}
	
	.app-channel {
		padding: 0 15px;
		border-top: 1px solid #fff;

		h3 {
			margin: 25px 0 0;
			font-size: 16px;
			text-align: center;
		}
	}

	.channel-list {
		display: -webkit-flex;

		& li {
			-webkit-flex: 1;
			width: 33.3%;
			padding-top: 30px;
			font-size: 14px;
			text-align: center;
		}
	}
}