@charset "utf-8";
.v-modal {
    position: fixed;
    left: 0;
    top: 44px;
    width: 100%;
    height: 100%;
    opacity: .7;
    background: #000;
    z-index: 9999;
}

.msgbox {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 80%;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 4px;
    z-index: 99999;
    &-header {
        padding: 30px 30px 30px 30px;
        &-title {
            font-size: 16px;
            line-height: 18px;
            color: #333;
            text-align: center;
        }
    }
    &-content {
        margin-top: -10px;
        padding: 0 10px 30px 10px;
        &-message {
            font-size: 14px;
            line-height: 18px;
            color: #999;
            text-align: center;
        }
    }
    &-btns {
        position: relative;
        display: flex;
        text-align: center;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 1px;
            background: #d8d8d8;
            transform: scaleY(0.5);
        }
        .msgbox-btn {
            position: relative;
            padding: 16px 0;
            flex: 1;
            font-size: 16px;
            line-height: 16px;
            &:nth-child(2)::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                height: 100%;
                width: 1px;
                background: #d8d8d8;
                transform: scaleX(0.5);
            }
            &-assist {
                color: #666666;
            }
            &-main {
                color: #4477ff;
            }
        }
    }
}
