@charset "utf-8";
//-----------------------------------------------------
// mixin scss
//-----------------------------------------------------
// Transitions
@mixin transition($transition...) {
    -webkit-transition: $transition;
    -moz-transition: $transition;
    transition: $transition;
}

// transform rotateZ
@mixin transformZ($rotate-deg) {
    -webkit-transform: rotateZ($rotate-deg);
    -ms-transform: rotateZ($rotate-deg);
}

// transform translate
@mixin translate($translate-width...) {
    -webkit-transform: translate3D($translate-width);
    transform: translate3D($translate-width);
}

// shadows
@mixin box-shadow($shadow...) {
    -webkit-box-shadow: $shadow;
    box-shadow: $shadow;
}

// border radius
@mixin border-radius($border-radius) {
    -webkit-border-radius: $border-radius;
    border-radius: $border-radius;
}

//background gradient
@mixin linear-gradient($color-stop...) {
    background-image: -webkit-linear-gradient($color-stop...);
    background-image: linear-gradient($color-stop...);
}

// clearfix
@mixin clearfix {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
        overflow: hidden;
    }
}

// text ellipsis
%textellipsis {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

@mixin txt-ellipsis($line:1) {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
    overflow: hidden;
}

// arrow
@mixin arrow( $direction, $width, $height, $color:#999) {
    @if not index( "up" "right" "down" "left", $direction) {
      @error "Direction must be `up`, `right`, `down`, `left`.";
    }
    @else if not _is-color($color) {
      @error "`#{$color}` is not a valid color for the `$color` argument in "+"the `arrow` mixin.";
    }
    @else {
      display: inline-block;
      height: $height;
      width: $width;
      border-top: 1px solid $color;
      border-right: 1px solid $color;
      @if $direction=="up" {
        transform: rotate(-45deg);
      }
      @else if $direction=="right" {
        transform: rotate(45deg);
      }
      @else if $direction=="down" {
        transform: rotate(135deg);
      }
      @else if $direction=="left" {
        transform: rotate(-135deg);
      }
    }
  }