// popup

.mask-layer {
	position: fixed;
	z-index: 99;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.8);
}

.popup-new {
	position: fixed;
	z-index: 101;
	top: 80px;
	left: 50%;
	margin-left: -150px;
	width: 300px;
	background-color: #fff;
	border-radius: 3px;

	.pop-head {
		position: relative;
		height: 105px;
		background-color: #C9ECFF;
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;
	}

	.cartoon-layer {
		position: absolute;
		top: -36px;
		left: 50%;
		width: 130px;
		height: 130px;
		margin-left: -65px;
		background: url(../images/mask_cartoon.png) 0 0 no-repeat;
		background-size: 130px 210px;
	}
	.cloud-layer {
		position: absolute;
		top: 0;
		left: 0;
		width: 300px;
		height: 105px;
		background: url(../images/mask_cloud.png) -37px 0 no-repeat;
		background-size: 374px 320px;
	}

	.pop-content {
		margin-bottom: 20px;

		h3 {
			margin-bottom: 10px;
			color: #5d57d0;
			font-size: 24px;
			text-align: center;
		}
	}
	.newlist {
		padding: 0 10px;

		& > li {
			margin-left: 20px;
			font-size: 14px;
			list-style: decimal;
		}
	}

	.pop-foot {
		position: relative;
		z-index: -1;
		height: 86px;		
		overflow: hidden;

		.mask-cicle {
			position: absolute;
			top: 0;
			width: 380px;
			margin-left: -40px;
			height: 86px;
			background-image: linear-gradient(90deg, #5884F0 0%, #5D55CE 100%);
			border-radius: 190px 190px 3px 3px / 60px 60px 3px 3px;
		}

		.button-wrap {
			position: relative;
			z-index: 2;
			padding-top: 30px;
			text-align: center;
		}	
		.btn-pop-confirm, .btn-pop-cancel {
			display: inline-block;
			width: 106px;
			height: 30px;
			line-height: 30px;
			border-radius: 15px;
			border: 1px solid #fff;
		}
		.btn-pop-confirm {
			color: #5976e5;
			background-color: #fff;
		}
		.btn-pop-cancel {
			margin-left: 10px;
			color: #fff;			
		}
	}
}