// 差标控制
.standard {
  padding-top: 44px;
  padding-bottom: 54px;
  .fix-bottom-bar {
    bottom: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #f0f0f0;
    .btn-linear {
      .plus {
        display: inline-block;
        font-size: 28px;
        vertical-align: middle;
        margin: -10px 5px 0 0;
      }
      .txt {
        vertical-align: top;
      }
    }
  }
}

.navbar {
  display: flex;
  position: fixed;
  top: 44px;
  left: 0;
  right: 0;
  width: 100%;
  padding-top: 1px;
  background: #F0F0F0;
  z-index: 999;
  @include clearfix;
  .nav-item {
    flex: 1;
    height: 42px;
    line-height: 42px;
    font-size: 14px;
    text-align: center;
    color: #333;
    background: #fff;
    span {
      display: inline-block;
      &.cur {
        color: #3A70FF;
        border-bottom: 2px solid #4175FE;
      }
    }
  }
}

.standard-content {
  margin-top: 44px;
  .standard-entry {
    position: absolute;
    z-index: 2;
    bottom: 30px;
    left: 0;
    width: 100%;
    color: #3a70ff;
    font-size: 14px;
    text-align: center;
  }
}

// 未设置
.unset-info {
  padding-top: 120px;
  text-align: center;
  font-size: 14px;
  &>p {
    margin: 20px 0;
    color: #999;
  }
  .more {
    width: 270px;
    height: 110px;
    margin: 0 auto;
    padding: 0 25px;
    border: 1px solid #D3D8EA;
    border-radius: 5px;
  }
  .more-text {
    margin-bottom: 10px;
    padding: 10px 0;
    border-bottom: 1px solid #D3D8EA;
    strong {
      font-size: 16px;
    }
  }
}
.ds-none{
    display: none;
}
.step-cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url(http://pic.c-ctrip.com/corp_niv/me/platform/mess-leading.png);
  background-position:top center;
  background-size: cover;
  z-index: 999999; 
}