@charset "utf-8";
.deputy-management {
  padding-top: 98px;
  .list {
    padding-left: 10px;
    background: #fff;
    .item {
      position: relative;
      padding: 10px 15px 10px 0;
      &:not(:last-of-type) {
        border-bottom: 1px solid #d8d8d8;
      }
      .wrapper {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 15px;
          @include arrow('right', 10px, 10px)
        }
        .title {
          span,
          i {
            vertical-align: middle;
            display: inline-block;
          }
          .name {
            font-size: 14px;
          }
          .tel {
            font-size: 12px;
            color: $font-color-light;
            margin-top: 5px;
          }
        }
        .note {
          font-size: 12px;
          line-height: 12px;
          color: $font-color-light;
        }
        &-slip {
          transform: translateX(-54px);
          &::after {
            display: none;
          }
        }
      }
      .delete {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 54px;
        line-height: 54px;
        color: #fff;
        font-size: 14px;
        text-align: center;
        background-color: #FE0000;
      }
    }
  }
  .fix-bottom-bar {
    bottom: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #f0f0f0;
    .btn-passed {
      line-height: 38px;
    }
    .plus {
      font-size: 26px;
      margin-top: -3px;
      margin-right: 7px;
    }
    span {
      display: inline-block;
      vertical-align: middle;
    }
  }
}

.deputy-employee {
  padding-top: 45px;
  .tab {
    display: flex;
    height: 40px;
    width: 100%;
    background: #fff;
    .item {
      flex: 1;
      text-align: center;
      span {
        display: inline-block;
        color: $font-color-gray;
        padding: 10px 0;
        width: 82px;
        text-align: center;
        border-bottom: 2px solid #fff;
      }
      &.active {
        span {
          color: #4175FE;
          border-bottom: 2px solid #4175FE;
        }
      }
    }
  }
  .fixed-input {
    position: relative;
    top: 0;
  }
  .select-box {
    padding-bottom: 44px;
    ul {
      padding-left: 12px;
      background: #fff;
      li {
        color: #333;
        font-size: 14px;
        line-height: 14px;
        p {
          padding-bottom: 20px;
          padding-right: 12px;
        }
        &.active {
          color: #4175FE;
        }
        span,
        i {
          vertical-align: middle;
        }
        &.first {
          padding-top: 20px;
          font-size: 16px;
          line-height: 16px;
          &:not(:last-of-type) {
            border-bottom: 1px solid #d8d8d8;
          }
        }
        .icon-deputy {
          margin-left: 5px;
        }
      }
    }
  }
  .hide-tap {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 10px 12px;
    background: #EBEBEB;
    font-size: 14px;
    .highlight {
      display: inline-block;
      width: 24px;
      height: 24px;
      line-height: 24px;
      background: #4477FF;
      border-radius: 50%;
      color: #fff;
      text-align: center;
    }
    .dtl {
      color: #4477FF;
    }
    .btn{
      float: right;
      width: 55px;
      height: 25px;
      line-height: 25px;
      color: #fff;
      font-size: 14px;
      background: #4477FF;
      border-radius: 4px;
      text-align: center;
    }
  }
  .show-tap {
    .pop-tab {
      display: flex;
      .item {
        flex: 1;
        padding: 10px 0;
        text-align: center;
        font-size: 14px;
        line-height: 14px;
        &.active {
          color: #4477FF;
          border-bottom: 1px solid #4477FF;
        }
      }
    }
    .list {
      padding-left: 10px;
      padding-bottom: 50px;
      ul {
        .item {
          font-size: 14px;
          padding: 15px 12px 15px 0;
          border-bottom: 1px solid #dbdbdb;
        }
      }
    }
  }
}
@keyframes popbottom
{
  from{
    transform: translate3d(0,100%,0);
  }
  to{
    transform: translate3d(0,0,0);

  }
}
@-webkit-keyframes popbottom
{
  from{
    transform: translate3d(0,100%,0);
  }
  to{
    transform: translate3d(0,0,0);

  }
}