// 帮助中心
.help {
  padding-top: 44px;
  .banner {
    margin: 10px 0;
  }
}

.customer-panel {
  display: -webkit-flex;
  width: 220px;
  height: 42px;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 21px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .3);
  .customer-button {
    -webkit-flex: 1;
    line-height: 42px;
    text-align: center;
    &+.customer-button {
      border-left: 1px solid #EEEEEE;
    }
  }
}

.grid-inner-group {
  padding-left: 58px;
  border-bottom: 1px solid #F6F6F6;
  .grid-panel {
    .grid {
      min-height: 44px;
    }
    .menu-name {
      line-height: 20px;
    }
  }
}

// 问题反馈
.feedback {
  padding: 10px 15px;
  color: #999;
  .button-wrap {
    margin-top: 50px;
  }
}

.feed-title {
  margin-bottom: 10px;
  font-size: 16px;
}

.feed-type {
  margin-bottom: 5px;
  .type-item {
    display: inline-block;
    padding: 5px;
    margin-bottom: 5px;
    border: 1px solid #ccc;
    background-color: #fff;
    border-radius: 3px;
    &.cur {
      color: #4578FF;
      border-color: #4578FF;
    }
  }
}

.feed-context {
  position: relative;
  margin-bottom: 10px;
  .feed-textarea {
    width: 100%;
    height: 140px;
    padding: 5px;
    border: 1px solid #ccc;
    background-color: #fff;
    border-radius: 5px;
  }
  .word-counter {
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
}

.feed-upload {
  font-size: 14px;
  &>p {
    margin-bottom: 10px;
  }
}

.image-list {
  display: -webkit-flex;
  .image-item {
    position: relative;
    width: 62px;
    height: 62px;
    margin: 0 10px 10px 0;
  }
  .image-wrap {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: #7D7D7D;
    overflow: hidden;
  }
  img {
    width: 100%;
  }
  .image-button {
    margin-right: 0;
    padding-top: 5px;
    color: #06c;
    font-size: 12px;
    text-align: center;
    border: 1px dashed #06c;
    background-color: transparent;
    strong {
      font-size: 24px;
      line-height: 1;
    }
  }
  .btn-delete {
    position: absolute;
    top: -8px;
    right: -8px;
    width: 16px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    color: #fff;
    background-color: #333;
    border-radius: 50%;
  }
}

.help-image {
  font-size: 0;
  img {
    width: 100%;
  }
}

.help-settle {
  padding: 59px 15px 15px 15px;
  font-size: 14px;
  background-color: #EEF9FF;
  .settle-solgon {
    position: relative;
    margin-bottom: 15px;
    padding: 20px;
    color: #0077C9;
    .sign-left,
    .sign-right {
      position: absolute;
      width: 15px;
      height: 15px;
      border: 2px solid #0077C9;
    }
    .sign-left {
      top: 0;
      left: 0;
      border-width: 2px 0 0 2px;
    }
    .sign-right {
      bottom: 0;
      right: 0;
      border-width: 0 2px 2px 0;
    }
  }
  .settle-card {
    margin-bottom: 10px;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
  }
  .settle-features {
    dt {
      margin-bottom: 10px;
      text-align: center;
    }
    dd {
      color: #999;
    }
  }
  .settle-content {
    dt {
      text-align: center;
      margin-bottom: 5px;
    }
    dd {
      padding: 10px 0;
      color: #999;
      &:not(:last-child) {
        border-bottom: 1px dashed #ccc;
      }
      &:last-child {
        padding-bottom: 0;
      }
      a {
        color: #0066cc;
        display: block;
        margin-top: 5px;
      }
    }
  }
  .settle-tip {
    position: relative;
    padding: 0 10px 0 27px;
    color: #ccc;
    font-size: 12px;
    .icon-warn {
      position: absolute;
      left: 8px;
      top: 2px;
      width: 12px;
      height: 12px;
      background-image: url('//pic.c-ctrip.com/corp_niv/cui/un_icons.png');
      background-position: -140px -432px;
    }
  }
  .help-list {
    margin-top: 10px;
    padding-top: 10px;
    line-height: 2;
    border-top: 1px solid #EBEBEB;
  }
}

.help-new {
  margin-top: 44px;
  h1 {
    color: #0077c9;
    font-size: 18px;
    text-align: center;
    padding: 20px 0 63px 0;
    background-color: #d8efff;
    background-image: url('http://pic.c-ctrip.com/corp_niv/me/platform/cloud.png');
    background-position: bottom;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .list {
    .item {
      padding: 15px 0 1px 0;
      &:nth-child(odd) {
        background: #fff;
      }
      &:nth-child(even) {
        background: #eef8ff;
      }
      h2 {
        position: relative;
        color: #007dd2;
        font-size: 14px;
        padding-left: 14px;
        margin-bottom: 5px;
        .num {
          position: absolute;
          left: 14px;
          top: 0;
          height: 19px;
          width: 19px;
          line-height: 19px;
          text-align: center;
          border-radius: 50%;
          color: #fff;
          background: #0077c9;
          font-family: Impact, Charcoal, sans-serif;
        }
        span {
          &:last-child {
            display: inline-block;
            padding-left: 22px;
          }
        }
      }
      .des {
        font-size: 12px;
        color: #333;
        padding: 0 38px;
        margin-bottom: 10px;
      }
      img {
        display: block;
        height: auto;
        margin: 0 auto 12px auto;
      }
    }
    &-one {
      img {
        width: 80%;
      }
    }
    &-two {
      img {
        width: 84%;
      }
    }
    &-three {
      img {
        width: 88%;
      }
    }
  }
  .grey-line {
    margin: 10px auto 10px auto;
    width: 90%;
    height: 0;
    border-bottom: 1px dashed #ccc;
  }
}