@charset "utf-8";
// popbox
.popup-box {
  position: fixed;
  left: 50%;
  width: 100%;
  backface-visibility: hidden;
  background: #fff;
  z-index: 10000;
  transform: translate3d(-50%, 0, 0);
  &.bottom {
    bottom: 0;
  }
  &-header {
    position: relative;
    padding: 20px 0;
    text-align: center;
    font-size: 18px;
    line-height: 18px;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 1px;
      background: #dcdcdc;
      transform: scaleY(0.5);
    }
  }
  &-list {
    padding-left: 12px;
    font-size: 16px;
    max-height: 300px;
    overflow: scroll;
  }
  &-item {
    position: relative;
    padding: 24px 10% 24px 0;
    &:not(:last-child)::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 1px;
      background: #dcdcdc;
      transform: scaleY(0.5);
    }
    .disable {
      color: #999;
    }
  }
  &-txt {
    line-height: 16px;
  }
  .icon-checked {
    position: absolute;
    top: 50%;
    right: 5%;
    margin-top: -7px;
  }
}

