// switch
.field-switch {
  float: right;
  font-size: 0;
  line-height: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-tap-highlight-color: rgba(0, 0, 0, 0);
  -o-tap-highlight-color: rgba(0, 0, 0, 0);
  tap-highlight-color: rgba(0, 0, 0, 0);
  &-input {
    display: none;
    &:checked+.field-switch-core {
      border-color: #45db5e;
      background-color: #45db5e;
    }
    &:checked+.field-switch-core::before {
      transform: scale(0);
    }
    &:checked+.field-switch-core::after {
      transform: translateX(20px);
    }
    &:disabled+.field-switch-core {
      border-color: #b5f1bf;
      background-color: #b5f1bf;
    }
    &:disabled+.field-switch-core::before {
      transform: scale(0);
    }
    &:disabled+.field-switch-core::after {
      transform: translateX(20px);
    }
  }
  &-core {
    display: inline-block;
    position: relative;
    width: 52px;
    height: 32px;
    border: 2px solid #e5e5e5;
    border-radius: 16px;
    box-sizing: border-box;
    background: #e5e5e5;
    &:before {
      content: " ";
      top: 0;
      left: 0;
      position: absolute;
      width: 48px;
      height: 28px;
      background-color: #fdfdfd;
      transition: transform .3s;
      border-radius: 14px;
    }
    &:after {
      content: " ";
      top: 1;
      left: 0;
      position: absolute;
      width: 28px;
      height: 28px;
      background-color: #fff;
      box-shadow: 0 1px 3px rgba(0, 0, 0, .4);
      transition: transform .3s;
      border-radius: 14px;
    }
  }
}

// checkbox
.field-checkbox {
  float: right;
  font-size: 0;
  &-input {
    display: none;
    &:checked+.field-checkbox-core {
      &::before {
        position: absolute;
        z-index: 3;
        top: 0;
        right: -1px;
        width: 12px;
        height: 5px;
        border-color: #4175FD;
        border-width: 0 0 1px 1px;
        border-style: solid;
        transform: rotateZ(-45deg) scale(1);
        content: "";
      }
      &::after {
        position: absolute;
        top: -1px;
        right: -1px;
        width: 3px;
        height: 5px;
        background-color: #fff;
        content: "";
      }
    }
  }
  &-core {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 1px solid #4175FE;
    border-radius: 3px;
    vertical-align: middle;
    &::before {
      position: absolute;
      z-index: 3;
      content: "";
      top: 0;
      right: -1px;
      width: 12px;
      height: 5px;
      border-color: #4175FD;
      border-width: 0 0 1px 1px;
      border-style: solid;
      transform: rotateZ(-45deg) scale(0);
      transition: transform .2s, -webkit-transform .2s;
    }
  }
  &.disable{
    .field-checkbox-core{
      border: 1px solid #ccc;
    }
  }
}

