@charset "utf-8";
//-----------------------------------------------------
// reset scss
//-----------------------------------------------------
/* Broswer Reset */

body,
h1,
h2,
h3,
h4,
dl,
dt,
dd,
ul,
ol,
li,
p,
menu,
nav {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  font-size: 100%;
  font-weight: normal;
}

ul,
ol,
li {
  list-style: none;
}

button,
img,
select {
  border: none;
  margin: 0;
  outline: none;
}

input {
  margin: 0;
  outline: none;
}

a {
  color: #0066cc;
  text-decoration: none;
  &:active {
    color: #FF7E01;
    -webkit-tap-highlight-color: #c8c8c8;
  }
}

body {
  color: #333;
  background-color: #fff;
  -webkit-overflow-scrolling: touch;
  -webkit-touch-callout: none;
}

body,
button,
input,
select {
  font: normal 12px/1.5 "PingFang SC", "Microsoft Yahei", Tahoma, Arial, "Hiragino Sans GB", Simsun, STXihei, Sans-serif; // 优先级 1: MAC; 2: 英文; 3: 中文;
}

i,
dfn,
em {
  font-style: normal;
}

dfn {
  font-family: Arial;
}

html,
body {
  -webkit-overflow-scrolling: touch;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  box-sizing: border-box;
}

html {
  touch-action: manipulation;
  min-height: 100%;
  height: 100%;
}