/*整站公共布局*/
.mt10{
  margin-top: 10px;
}
//白色卡片
%card {
  background-color: #fff;
  line-height: 24px;
  padding: 10px; //+radius
  &.border {
    border-radius: 5px;
  }
  h2 {
    font-size: 14px;
    span {
      vertical-align: middle;
      margin-left: 3px;
      color: #999999;
    }
  }
}

.flex-row {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  &.flex-border {
    border-bottom: 1px solid #e4e4e4;
  }
  .flex-row-1 {
    width: 25%;
    &.flex22 {
      width: 22%;
    }
    &.right {
      text-align: right;
    }
    &.gray {
      color: #999;
    }
  }
  .flex-row-3 {
    width: 75%;
  }
}

/** 水平伸缩容器**/

.row-flex {
  -moz-box-orient: horizontal;
  -webkit-box-orient: horizontal;
  -moz-box-direction: normal;
  -webkit-box-direction: normal;
  -moz-box-lines: multiple;
  -webkit-box-lines: multiple;
  -ms-flex-flow: row wrap;
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
}

.midCenter {
  /** 垂直居中核心**/
  -moz-box-pack: center;
  -webkit-box-pack: center;
  box-pack: center;
  -moz-box-align: center;
  -webkit-box-align: center;
  box-align: center;
  box-pack: center;
  -ms-flex-pack: center;
  display: -ms-flexbox;
  -ms-flex-align: center;
  justify-content: center;
  align-items: center;
}

//右侧编辑
.edit-vip-pop {
  position: absolute;
  right: 15px;
  top: 40px;
  width: 70px;
  z-index: 9998;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  border-radius: 3px;
  dt,
  dd {
    margin: 0 10px;
    text-align: center;
    padding: 5px 0;
  }
  dt {
    border-bottom: 1px solid #d8d8d8;
  }
}

//公用弹出
.base-module {
  border-radius: 5px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 50%;
  top: 50%;
  width: 70%;
  font-size: 14px;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: #fff;
  z-index: 10000;
  &-main {
    padding: 15px;
    p {
      line-height: 18px;
      margin-bottom: 3px;
      &.center {
        text-align: center;
      }
    }
    input {
      border: 0 none;
      background: #ececec;
      border-radius: 2px;
      padding-left: 5px;
      height: 30px;
      line-height: 30px;
      width: 100%;
    }
  }
  &-btn {
    border-top: 1px solid #d8d8d8;
    a {
      float: left;
      width: 50%;
      text-align: center;
      padding: 10px 0;
      &:first-child {
        border-right: 1px solid #d8d8d8;
      }
    }
  }
}

//部分公用样式
.normal-wrapper {
  padding-top: 54px;
}

.add-info {
  padding: 14px 0;
  text-align: center;
  border-bottom: 1px solid #eee;
  background: #fff;
  color: #4c7cff;
  font-size: 15px;
  line-height: 15px;
  span.plus {
    font-size: 20px;
    line-height: 15px;
    margin-right: 3px;
  }
}

.edit-header {
  padding: 10px 15px;
  border-bottom: 1px solid #f1f1f1;
  background-color: #fff;
  p {
    padding-left: 8px;
    border-left: 3px solid #6286e9;
    font-size: 14px;
    line-height: 14px;
    color: #666;
  }
}

.fix-bottom-bar {
  padding: 10px 0;
  background: #f0f0f0;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  .flexbox {
    display: -webkit-flex;
    height: 44px;
    .flex-item {
      -webkit-flex: 1;
      padding: 0 12px;
    }
  }
  .btn-passed,
  .btn-refuse {
    display: inline-block;
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 16px;
    text-align: center;
    border-radius: 3px;
    border-width: 1px;
    border-style: solid;
  }
  .btn-passed {
    color: #fff;
    background-color: #4175FD;
    border-color: #4175FD;
  }
  .btn-refuse {
    color: #4175FD;
    background-color: #fff;
    border-color: #ECECEC;
  }
  .btn-linear {
    background-image: linear-gradient(90deg, #5884F0 0%, #5D55CE 100%);
  }
}

.hide-tap {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px 12px;
  background: #EBEBEB;
  font-size: 14px;
  z-index: 9;
  .highlight {
    display: inline-block;
    width: 24px;
    height: 24px;
    line-height: 24px;
    background: #4477FF;
    border-radius: 50%;
    color: #fff;
    text-align: center;
  }
  .dtl {
    color: #4477FF;
  }
  .btn {
    float: right;
    width: 55px;
    height: 25px;
    line-height: 25px;
    color: #fff;
    font-size: 14px;
    background: #4477FF;
    border-radius: 4px;
    text-align: center;
  }
}

.show-tap {
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 99999;
  animation: popbottom 0.2s ease-in-out;
  .header {
    padding: 10px 12px;
    background: #EBEBEB;
    font-size: 14px;
    .highlight {
      display: inline-block;
      width: 24px;
      height: 24px;
      line-height: 24px;
      background: #4477FF;
      border-radius: 50%;
      color: #fff;
      text-align: center;
    }
    .btn {
      float: right;
      width: 55px;
      height: 25px;
      line-height: 25px;
      color: #fff;
      font-size: 14px;
      background: #4477FF;
      border-radius: 4px;
      text-align: center;
    }
    .return {
      color: #4477FF;
    }
  }
}

@keyframes popbottom {
  from {
    transform: translate3d(0, 100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes popbottom {
  from {
    transform: translate3d(0, 100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

.search-list {
  font-size: 14px;
  color: #222222;
  ul {
    padding-left: 13px;
    li {
      padding: 12px 0;
      border-bottom: 1px solid #e9e9e9;
    }
  }
}

.search-box {
  padding: 10px;
  background: #f0f0f0;
  .wrapper {
    position: relative;
    padding: 5px 22px 5px 33px;
    background: #f2f2f2;
    border-radius: 4px;
    background: #fff;
    input {
      border: 0;
      background-color: transparent;
      width: 100%;
      font-size: 14px;
      line-height: 18px;
    }
    .icon-payment {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      &-search {
        left: 10px;
      }
      &-search-big {
        left: 9px;
      }
      &-close {
        right: 5px;
      }
    }
  }
}

.fixed-input {
  width: 100%;
  position: fixed;
  left: 0;
  top: 44px;
  background-color: #F0F0F0;
  z-index: 6;
}

.note-box {
  padding: 10px 0;
  position: relative;
  padding-left: 28px;
  padding-right: 18px;
  font-size: 12px;
  color: #999;
  i {
    position: absolute;
    left: 10px;
    top: 12px;
  }
  .active {
    color: #4175FD;
  }
}

.mess-qs {
  position: relative;
  padding-left: 15px;
  margin-bottom: 10px;
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    background: url('http://pic.c-ctrip.com/corp_niv/cui/icon_ask.png') no-repeat 0/cover;
  }
  a {
    color: #4175fe;
    text-decoration: underline;
  }
}

.tip-content{
  padding: 10px 20px;
  position: fixed;
  top: 55px;
  color: #fff;
  font-size: 14px;
  z-index: 10000;
  .title{
    font-size: 16px;
    text-align: center;
    margin-bottom: 5px;
  }
  .con{
    margin-bottom: 20px;
    .focus{
      color: #f7ef6b;
    }
  }
}