@charset "utf-8";
/* LAYOUT */

body {
  max-width: 540px;
  margin: 0 auto;
  background-color: #F0F0F0;
}

* {
  box-sizing: border-box;
}

// header
.m-header,
.account-board {
  background-image: linear-gradient(90deg, #5884F0 0%, #5D55CE 100%);
}

.m-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 999;
  .wrapper {
    position: relative;
    padding: 0 60px;
  }
  .nav-left,
  .nav-right,
  .nav-close {
    position: absolute;
    top: 0;
  }
  .nav-left {
    left: 0;
  }
  .nav-right {
    right: 0;
  }
  .nav-close {
    left: 44px;
    width: 44px;
    height: 44px;
  }
  .icon-ins-close {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .heading {
    font-size: 16px;
    color: #fff;
    line-height: 44px;
    text-align: center;
  }
  &.whitebg {
    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, .1);
    .heading {
      color: #333;
    }
    .btn-nav-back {
      &:before {
        border-color: #999;
      }
    }
  }
}

.btn-nav-back {
  position: relative;
  display: block;
  width: 44px;
  height: 44px;
  &:before {
    position: absolute;
    top: 16px;
    left: 15px;
    width: 12px;
    height: 12px;
    border-style: solid;
    border-color: #fff;
    border-width: 0 0 1px 1px;
    content: "";
    transform: rotateZ(45deg);
  }
}

.btn-nav-txt {
  display: inline-block;
  width: 44px;
  height: 44px;
  line-height: 44px;
  text-align: right;
  color: #fff;
  font-size: 14px;
  margin-right: 15px;
  &.base {
    color: #333;
  }
}

.btn-nav-txt-white {
  display: inline-block;
  width: 60px;
  height: 44px;
  line-height: 44px;
  text-align: right;
  color: #666;
  font-size: 14px;
  margin-right: 15px;
  &.spec {
    font-weight: bold;
    font-size: 16px;
    margin-top: -2px;
  }
}

.btn-nav-txt-blue {
  display: inline-block;
  width: 44px;
  height: 44px;
  line-height: 44px;
  text-align: right;
  color: #4c7cff;
  font-size: 14px;
  margin-right: 15px;
}

.btn-open {
  position: relative;
  width: 150px;
  height: 36px;
  margin: 0 auto;
  line-height: 36px;
  font-size: 16px;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 18px;
}

.label-gift {
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 3px;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  background-color: #FF6600;
  border-radius: 3px;
  &:after {
    position: absolute;
    bottom: -8px;
    left: 5px;
    border-style: solid;
    border-color: transparent;
    border-width: 4px 3px;
    border-top-color: #FF6600;
    border-left-color: #FF6600;
    content: "";
  }
}

.account-info {
  display: -webkit-box;
  padding: 20px 0;
  .info-box {
    -webkit-flex: 1;
    width: 50%;
    padding: 0 15px;
    color: #fff;
    line-height: 1;
    &:first-child {
      border-right: 1px solid #8390E6;
    }
  }
  .info-title {
    margin-bottom: 10px;
    font-size: 16px;
  }
  .info-detail {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .price {
    font-size: 16px;
  }
}

.setting-info {
  position: relative;
  padding: 15px;
  color: #fff;
  background-color: rgba(0, 0, 0, .2);
  .setting-content {
    padding-right: 44px;
  }
  .setting-button {
    position: absolute;
    top: 0;
    right: 10px;
    height: 100%;
    &:after {
      display: inline-block;
      width: 0;
      height: 100%;
      content: "";
      vertical-align: middle;
    }
  }
  .btn-setting {
    display: inline-block;
    width: 44px;
    height: 24px;
    border: 1px solid #fff;
    border-radius: 3px;
    color: #fff;
    text-align: center;
    line-height: 22px;
    vertical-align: middle;
  }
}

.color-yellow {
  color: #FFCC00;
}

.help-info {
  padding: 10px 15px;
  color: #fff;
  background-color: rgba(255, 255, 255, .3);
  .icon-warn-xsm {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 100%;
    background-color: #FFB000;
    text-align: center;
    font-size: 12px;
    line-height: 1;
  }
  .info-link {
    float: right;
    color: #fff;
  }
  .arr-right-sm {
    display: inline-block;
    width: 8px;
    height: 8px;
    margin-left: 5px;
    border-color: #fff;
    border-style: solid;
    border-width: 1px 1px 0 0;
    transform: rotateZ(45deg);
  }
}

.grid-group {
  margin-bottom: 10px;
  background-color: #fff;
}

.grid-panel {
  display: -webkit-flex;
  border-bottom: 1px solid #F6F6F6;
  &:last-of-type {
    border-bottom: none;
  }
  .grid {
    padding: 12px 0;
    min-height: 58px;
  }
  .grid-1 {
    width: 58px;
    text-align: center;
    &:before {
      display: inline-block;
      height: 100%;
      line-height: 100%;
      content: "";
      vertical-align: middle;
    }
  }
  .grid-2 {
    position: relative;
    -webkit-flex: 1;
    padding-right: 40px;
    line-height: 18px;
  }
  .menu-title {
    font-size: 14px;
  }
  .menu-subtitle {
    font-size: 12px;
    color: #999;
  }
  .menu-name {
    font-size: 14px;
    line-height: 36px;
  }
  .arr-right-grid {
    position: absolute;
    top: 50%;
    right: 15px;
    width: 10px;
    height: 10px;
    margin-top: -5px;
    border-style: solid;
    border-color: #A7A7A7;
    border-width: 1px 1px 0 0;
    transform: rotateZ(45deg);
    &.arr-dw {
      transform: rotateZ(135deg);
    }
  }
}

// field grid
.field-panel {
  font-size: 14px;
  margin-bottom: 10px;
  .field-title {
    padding: 0 10px;
    color: #999;
  }
  .field-grid {
    padding-left: 10px;
    background-color: #fff;
  }
  .field-row {
    display: -webkit-flex;
    padding: 10px 15px 10px 0;
    line-height: 24px;
    border-bottom: 1px solid #eee;
    &:last-child {
      border-bottom: none;
    }
    [class^="col-"] {
      position: relative;
    }
    .col-1-1 {
      width: 100%;
    }
    .col-4-3 {
      width: 75%;
    }
    .col-4-1 {
      width: 25%;
    }
    .col-4-2 {
      width: 50%;
    }
    .col-align-right {
      text-align: right;
    }
    .col-multiline {
      span {
        display: block;
      }
      .txt-xsm {
        font-size: 12px;
        line-height: 1;
      }
    }
    .lh32 {
      line-height: 32px;
    }
    .merge-row {
      display: -webkit-flex;
      &:not(:last-child) {
        padding-bottom: 10px;
        border-bottom: 1px solid #eee;
      }
      &:not(:first-child) {
        padding-top: 10px;
      }
    }
    .input-box {
      position: relative;
      padding-right: 20px;
      .icon-close {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
      }
    }
    .grey-note {
      display: inline-block;
      font-size: 12px;
      line-height: 28px;
      color: #ccc;
      vertical-align: bottom;
    }
    .pwd-eye{
      position: relative;
      padding-right: 20px;
      .icon-eye{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 20px;
        background-image: url(http://pic.c-ctrip.com/corp_niv/me/platform/un_icon_book.png);
        background-repeat: no-repeat;
        &-open{
          background-position: 0 -140px;
        }
        &-close{
          background-position: 0 -160px;
        }
      }
    }
    .icon-qa {
      display: inline-block;
      width: 16px;
      height: 16px;
      line-height: 16px;
      color: #fff;
      text-align: center;
      background-color: #A9CCFE;
      border-radius: 100%;
    }
  }
  .field-row-multiple {
    padding: 0 15px 0 0;
    .inner-row {
      position: relative;
      border-bottom: 1px solid #eee;
      &:last-child {
        border-bottom: 0;
      }
      .wrapper {
        padding: 10px 15px 10px 30px;
      }
      .txt-ellipsis {
        width: 100%;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
      }
      .txt-add {
        color: #4477ff;
      }
      &-delete {
        .wrapper {
          padding: 10px 50px 10px 30px
        }
        .del-block {
          position: absolute;
          right: -15px;
          top: 0;
          width: 60px;
          height: 100%;
          background: #FF0000;
          color: #fff;
          font-size: 14px;
          span {
            display: inline-block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
    .col-4-1 {
      position: relative;
      .txt-middle {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .icon {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      &-delete-rotate {
        transform: translate(0, -50%) rotate(90deg);
      }
    }
  }
  .txt-active {
    color: #3a70ff;
  }
  .txt-default {
    color: #CCCCCC;
  }
  .form-control {
    width: 100%;
    height: 24px;
    font-size: 14px;
    border: 0;
    &::-webkit-input-placeholder {
      color: #ccc;
    }
  }
  .split-input {
    height: 24px;
    font-size: 14px;
    border: 0;
    &:first-child {
      width: 20%;
      border-right: 1px solid #eee;
    }
    &:nth-child(2) {
      width: 45%;
      text-indent: 3px;
      border-right: 1px solid #eee;
    }
    &:nth-child(3) {
      text-indent: 3px;
      width: 30%;
    }
  }
  .form-textarea {
    width: 100%;
    border: 0;
    &::-webkit-input-placeholder {
      color: #ccc;
    }
  }
  .arr-right-grid {
    position: absolute;
    top: 50%;
    right: 0;
    width: 10px;
    height: 10px;
    margin-top: -5px;
    border-style: solid;
    border-color: #A7A7A7;
    border-width: 1px 1px 0 0;
    transform: rotateZ(45deg);
  }
  .disable {
    color: #999;
  }
}

// 发票信息
.invoice {
  .cost{
    padding-top: 0;
  }
  .invoice-panel {
    margin-bottom: 10px;
    padding: 10px 10px 0;
    line-height: 24px;
    background-color: #fff;
  }
  .invoice-title {
    color: #999;
    padding-right: 30px;
  }
  .icon-label-invoice {
    margin-right: 5px;
  }
  .invoice-list {
    padding: 0 10px;
  }
  .field-panel {
    padding-top: 0;
    margin-bottom: 20px;
  }
  .field-title {
    margin-bottom: 10px;
  }
  .panel-title {
    padding: 0 10px;
    margin-bottom: 10px;
    font-size: 14px;
    color: #999;
  }
  .txt-bold {
    font-weight: bold;
  }
}

.grid-invoice {
  padding: 5px 0;
  &:not(:last-child){
    border-bottom: 1px solid #E4E4E4;
  }
  .invoice-row {
    @include clearfix;
    .col-label {
      float: left;
      width: 27%;
      color: #999;
      text-align: right;
    }
    .col-content {
      float: left;
      width: 73%;
      padding-left: 10px;
    }
  }
  &.hide {
    height: 82px;
    overflow: hidden;
  }
}

.invoice-handle {
  padding: 10px;
  color: #2B64FF;
  text-align: right;
}

.btn-paimary {
  display: block;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #fff;
  text-align: center;
  background-image: linear-gradient(90deg, #5884F0 0%, #5D55CE 100%);
  border-radius: 5px;
}

// toast
.toast {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 999999;
  max-width: 58%;
  color: #fff;
  padding: 16px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  background-color: rgba(0, 0, 0, .7);
  border-radius: 5px;
}

// modal
.modal {
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 30px 15px;
  color: #fff;
  background-color: rgba(0, 0, 0, .7);
  h3 {
    margin-bottom: 15px;
    font-size: 18px;
    text-align: center;
  }
  p {
    margin-bottom: 20px;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

// index_new
.index-new {
  .ds-none {
    display: none;
  }
  .top,
  .fixed-header {
    span {
      position: relative;
      padding-left: 15px;
      display: inline-block;
      color: #4c7cff;
      font-size: 14px;
      &::before {
        content: '';
        position: absolute;
        left: 4px;
        top: 6px;
        @include arrow('left', 8px, 8px, #4c7cff);
      }
    }
    .contract {
      position: absolute;
      top: 9px;
      right: -22px;
      height: 24px;
      line-height: 24px;
      color: #fff;
      padding: 0 30px;
      font-size: 12px;
      background-image: linear-gradient(90deg, #5884f0 0%, #5d55ce 100%);
      border-radius: 12px;
      &::before {
        content: '';
        position: absolute;
        left: 7px;
        top: 3px;
        width: 16px;
        height: 17px;
        background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/index_new_icon.png');
        background-size: 95px 94.5px;
        background-position: -65px -38px;
      }
    }
  }
  .fixed-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 11.5px 10px;
    z-index: 9999;
    overflow: hidden;
    background: #eff3fd;
    -webkit-animation-name: fadeIn;
    -webkit-animation-duration: .4s;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-delay: 0s;
  }
  .section {
    position: relative;
    margin-bottom: 10px;
    background: #fff;
    &-one {
      padding: 11.5px 10px 0 10px;
      background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/index_new_header.png?20180814');
      background-color: #fff;
      background-position: top center;
      background-size: 100%;
      background-repeat: no-repeat;
      overflow: hidden;
      .content {
        position: relative;
        padding: 15px 12px 14px 12px;
        .avatar {
          float: left;
          height: 58px;
          width: 58px;
          border-radius: 50%;
          background-color: #ccc;
          background-position: center center;
          background-size: cover;
        }
        .name {
          position: relative;
          height: 58px;
          overflow: hidden;
          margin-left: 58px;
          padding: 0 20px 0 10px;
          font-size: 18px;
          color: $font-color-normal;
          font-weight: bold;
          &::after {
            content: '';
            position: absolute;
            right: 8px;
            top: 20px;
            @include arrow('right',
            10px,
            10px,
            #999999);
          }
        }
      }
      .entrance {
        width: 100%;
        img {
          width: 100%;
          height: auto;
        }
      }
      .list {
        overflow: hidden;
        padding-bottom: 16px;
        .item {
          position: relative;
          padding-top: 45px;
          width: 25%;
          float: left;
          text-align: center;
          color: $font-color-gray;
          font-size: 12px;
          &::before {
            content: '';
            position: absolute;
            top: 20px;
            left: 50%;
            width: 22px;
            height: 22px;
            transform: translate(-50%, 0);
            background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/un_icon_cmange.png?20180905');
            background-size: 22px 272px;
            background-repeat: no-repeat;
          }
          &:first-child::before {
            background-position: 0 -202px;
          }
          &:nth-child(2)::before {
            background-position: 0 0;
          }
          &:nth-child(3)::before {
            background-position: 0 -224px;
          }
          &:nth-child(4)::before {
            background-position: 0 -75px;
          }
          &:nth-child(5)::before {
            background-position: 0 -102px;
          }
          &:nth-child(6)::before {
            background-position: 0 -50px;
          }
          &:nth-child(7)::before {
            background-position: 0 -25px;
          }
          &:nth-child(8)::before {
            background-position: 0 -250px;
          }
        }
      }
      .detail-box {
        background: #fff;
        box-shadow: 0 0 10px rgba(0, 0, 0, .2);
        border-radius: 5px;
        font-size: 12px;
        .content {
          display: flex;
          padding: 0;
          .part {
            flex: 1;
            padding: 14px;
            .num {
              font-size: 20px;
              font-weight: bold;
              line-height: 35px;
              &-orange {
                color: $base-color-orange;
              }
              &-blue {
                color: #5a71e2;
              }
            }
          }
        }
        .tip {
          position: relative;
          padding: 6px;
          background: #fffcd8;
          font-size: 12px;
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
          .txt {
            position: relative;
            padding-left: 18px;
            width: 80%;
            @include txt-ellipsis;
            &::before {
              content: '';
              position: absolute;
              top: 50%;
              left: 2px;
              height: 12px;
              width: 12px;
              transform: translateY(-50%);
              background: url(//pic.c-ctrip.com/corp_niv/cui/icon_warn.png) no-repeat 0/cover;
            }
          }
          a {
            position: absolute;
            right: 6px;
            top: 50%;
            transform: translateY(-50%);
          }
        }
      }
      .entrance-box {
        position: relative;
        background: -moz-linear-gradient(left, #5884f0 0%, #5d55ce 99%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(left, #5884f0 0%, #5d55ce 99%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to right, #5884f0 0%, #5d55ce 99%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        overflow: hidden;
        border-radius: 5px;
        padding: 10px;
        .content {
          color: #fff;
          padding: 0;
          h3 {
            font-size: 16px;
            font-weight: bold;
            margin-bottom: 3px;
          }
          .p {
            font-size: 12px;
          }
        }
        .btn {
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          background-color: #fff;
          padding: 0 17px 0 9px;
          height: 20px;
          line-height: 20px;
          border-radius: 10px;
          &::after {
            content: '';
            position: absolute;
            right: 12px;
            top: 50%;
            height: 7px;
            width: 7px;
            border-top: 1px solid #7087f0;
            border-right: 1px solid #7087f0;
            transform: rotate(45deg) translateY(-75%);
          }
          .text {
            font-size: 12px;
            font-weight: bold;
            background: -webkit-linear-gradient(left, #5884f0, #5d55ce);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
        }
      }
    }
    &-two {
      padding: 12px;
      &::after {
        content: '';
        position: absolute;
        right: 18px;
        top: 28px;
        @include arrow('right',
        10px,
        10px,
        #999999);
      }
      .content {
        position: relative;
        padding-left: 70px;
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: 5px;
          transform: translate(0, -50%);
          width: 50px;
          height: 50px;
          background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/index_new_icon.png');
          background-size: 95px 94.5px;
          background-position: -5px -5px;
        }
        h2 {
          font-size: 16px;
          font-weight: bold;
          color: $font-color-normal;
        }
        .txt {
          font-size: 12px;
          color: $font-color-light;
        }
        .num {
          position: absolute;
          right: 17px;
          top: 50%;
          transform: translateY(-50%);
          font-size: 12px;
          color: #fff;
          background: red;
          width: 20px;
          height: 20px;
          line-height: 20px;
          border-radius: 50%;
          text-align: center;
        }
        .icon-hot {
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);
          width: 29px;
          height: 32px;
          background: url('//pic.c-ctrip.com/corp_niv/me/platform/platform_hot.png') no-repeat 0/cover;
        }
      }
    }
    &-three {
      .header {
        padding: 10px 15px;
        border-bottom: 1px solid #f1f1f1;
        p {
          padding-left: 8px;
          border-left: 3px solid #6286e9;
          font-size: 14px;
          line-height: 14px;
          color: $font-color-gray;
        }
      }
      .content {
        height: 105px;
        overflow: hidden;
        .list {
          padding: 15px 0 22px 0;
          font-size: 12px;
          color: $font-color-normal;
          line-height: 18px;
          display: -webkit-box;
          overflow-x: scroll;
          overflow-y: hidden;
          scroll-behavior: smooth;
          -webkit-overflow-scrolling: touch; 
          &::-webkit-scrollbar {
            display: none;
          }
          .item {
            height: 74px;
            &:first-child {
              padding-left: 15px;
            }
            width: 70%;
            &:first-child .wrapper::before {
              width: 25px;
              height: 24px;
              background-position: -65px -5px;
            }
            &:nth-child(2) .wrapper::before {
              width: 24px;
              height: 25px;
              background-position: -5px -65px;
            }
          }
          .wrapper {
            position: relative;
            padding: 10px 15px 10px 10px;
            background: #f8f8f8;
            margin-right: 16px;
            border-radius: 4px;
            .dtl {
              margin-left: 30px;
            }
            h3 {
              font-size: 14px;
              color: $font-color-normal;
              @include txt-ellipsis();
            }
            .txt {
              color: $font-color-light;
              font-size: 12px;
              @include txt-ellipsis(2);
            }
            &::before {
              content: '';
              position: absolute;
              top: 50%;
              left: 8px;
              transform: translateY(-50%);
              background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/index_new_icon.png');
              background-size: 95px 94.5px;
            }
          }
        }
      }
    }
    &-four {
      padding-top: 20px;
      .header {
        position: relative;
        padding: 0 12px;
        .txt {
          font-size: 15px;
          color: $font-color-normal;
          font-weight: bold;
        }
        .more {
          position: absolute;
          top: 2px;
          right: 0;
          padding-right: 27px;
          font-size: 12px;
          color: $font-color-light;
          &::after {
            content: '';
            position: absolute;
            right: 17px;
            top: 4px;
            @include arrow('right',
            8px,
            8px,
            #999999);
          }
        }
      }
      .content {
        .list-wrapper{
          height: 36px;
          overflow-y: hidden;
        }
        .list {
          padding-top: 10px;
          padding-bottom: 14px;
          display: -webkit-box;
          overflow-x: scroll;
          scroll-behavior: smooth;
          &::-webkit-scrollbar {
            display: none;
          }
          .item {
            padding-left: 12px;
            &.active .wrapper {
              border: 1px solid #4477FF;
              border-radius: 11px;
              color: #4477FF;
            }
            &:last-child {
              padding-right: 12px;
              margin-right: 12px;
            }
          }
          .wrapper {
            color: $font-color-light;
            font-size: 14px;
            text-align: center;
            height: 22px;
            line-height: 20px;
            padding: 0 10px;
          }
        }
        .txt {
          padding: 15px 0;
          font-size: 14px;
          color: $font-color-gray;
          &:not(:last-child) {
            border-bottom: 1px solid #dbdbdb;
          }
        }
        .question{
          padding: 0 12px;
        }
      }
    }
  }
  .bottom-line {
    padding: 25px 0;
    font-size: 14px;
    color: $font-color-light;
    text-align: center;
  }
}