// 引导页下载浮层
.lead-download {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 10px 10px 32px;
  background: #fff;
  font-size: 18px;
  color: #222;
  font-family: PingFangSC-Medium, sans-serif;
  span,
  i,
  a {
    display: inline-block;
    vertical-align: middle;
  }
  .icon-close {
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    &::before {
      content: '';
      position: absolute;
      top: 7px;
      left: 7px;
      width: 15px;
      height: 15px;
      background: url('//pic.c-ctrip.com/corp_niv/me/platform/icon-close.png') no-repeat 0/cover;
    }
  }
  .logo {
    width: 41px;
    height: 41px;
    background: url('//pic.c-ctrip.com/corp_niv/me/platform/logo.png') no-repeat 0/cover;
  }
  .btn-download {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    width: 115px;
    height: 35px;
    background: url('//pic.c-ctrip.com/corp_niv/me/platform/btn-download.gif') no-repeat 0/cover;
  }
}

// 键盘
.keyboard {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
  .cell {
    float: left;
    width: 33.33%;
    padding: 16px 0;
    text-align: center;
    font-size: 20px;
    line-height: 20px;
    color: #222;
    background: #fff;
    border-top: 1px solid #e4e4e4;
    &:not(:nth-child(3n)) {
      border-right: 1px solid #e4e4e4;
    }
    &:nth-child(10) {
      color: #666;
      background: #f1f1f1;
    }
    &:last-child {
      background: #f1f1f1;
    }
    &:active {
      background: #ededed;
    }
  }
}