@charset "utf-8";
.insurance-given {
  padding-top: 44px;
  .top-header {
    position: relative;
    border-bottom: 1px solid $base-border-color;
    overflow: hidden;
    background: #fff;
    .left {
      padding: 13px 0;
      position: relative;
      float: left;
      width: 63%;
      .icon-ins-present {
        position: absolute;
        top: 15px;
        left: 12px;
      }
      .content {
        padding-left: 40px;
        h3 {
          position: relative;
          color: #333;
          p {
            font-size: 18px;
            width: 96%;
            @include txt-ellipsis;
          }
          .arrow {
            position: absolute;
            right: 3px;
            top: 8px;
            @include arrow( 'right', 10px, 10px, #4477FF)
          }
        }
        .note {
          font-size: 12px;
          color: #B1B0B0;
          @include txt-ellipsis;
        }
      }
    }
    .right {
      position: absolute;
      top: 15px;
      right: 12px;
      .email {
        height: 24px;
        padding: 0 10px;
        color: #4477FF;
        font-size: 12px;
        line-height: 24px;
        border-radius: 12px;
        border: 1px solid #4477FF;
      }
    }
  }
  .banner {
    height: 145px;
    background: url('//pic.c-ctrip.com/corp_niv/me/platform/ins_banner.jpg') no-repeat center/cover;
  }
  .tab {
    display: flex;
    background: #fff;
    border: 1px solid $base-border-color;
    .item {
      flex: 1;
      padding: 12px 0;
      color: #333;
      font-size: 15px;
      border-bottom: 2px solid transparent;
      text-align: center;
      &.active {
        color: #4477FF;
        border-bottom: 2px solid #4477FF;
      }
      &.disabled {
        color: $font-color-light;
      }
    }
  }
  .list {
    padding: 12px;
    .block {
      background: #fff;
      border-radius: 4px;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
      .name {
        padding: 18px 0 15px 12px;
        position: relative;
        font-size: 15px;
        line-height: 15px;
        color: #333;
        font-weight: bold;
        border-bottom: 1px solid $base-border-color;
      }
      .icon-ins-close {
        position: absolute;
        right: 12px;
        top: 50%;
        transform: translateY(-50%);
      }
      ul {
        padding-left: 11px;
        .item {
          position: relative;
          font-size: 15px;
          padding: 18px 0 15px 0;
          color: #333;
          &:not(:last-child) {
            border-bottom: 1px solid $base-border-color;
          }
          .tit {
            display: inline-block;
            width: 30%;
            text-align: left;
          }
          input {
            font-size: 15px;
            width: 50%;
            border: none;
            font-size: #333;
          }
          .calendar {
            position: absolute;
            top: 50%;
            right: 12px;
            transform: translateY(-50%);
            text-align: center;
            font-size: 12px;
            color: #4477FF;
          }
        }
      }
    }
  }
  .add {
    margin: 0 12px 15px 12px;
    color: #4477FF;
    background: #fff;
    border-radius: 4px;
    padding: 15px 0;
    text-align: center;
    span,
    i {
      vertical-align: middle;
    }
  }
  .tip {
    position: relative;
    padding: 0 12px;
    margin-bottom: 76px;
    .content {
      color: $font-color-gray;
      padding-left: 22px;
      font-size: 12px;
      .highlight {
        color: #4477FF;
      }
    }
    .field-checkbox {
      position: absolute;
      left: 12px;
      top: 0;
    }
  }
  .fix-bottom-bar {
    bottom: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    background: #fff;
    .btn-linear {
      border: none;
      background-image: linear-gradient(90deg, #FFC300 0%, #FF9000 100%)
    }
  }
  .no-data {
    padding: 60px 0 39px 0;
    text-align: center;
    p {
      font-size: 15px;
      color: $font-color-light;
    }
  }
}

.insurance-list {
  background: #f8f8f8;
  .search-box {
    background: #f8f8f8;
  }
  .search-list {
    background: #fff;
    li:last-child {
      border: none;
    }
    .card {
      margin-top: 4px;
      font-size: 12px;
      color: $font-color-light;
      line-height: 12px;
    }
  }
}

.insurance-confirm {
  .msgbox {
    &-header {
      padding: 14px 0;
      background: #EBEEF3;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      &-title {
        color: $font-color-gray;
      }
    }
    &-content {
      margin-top: 0;
      padding: 35px 15px;
      input {
        font-size: 14px;
        color: #333;
        width: 100%;
        border: none;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid #D8D8D8;
      }
      &-message {
        font-size: 16px;
        color: #333;
        margin: 5px 0;
      }
    }
    &-btn {
      &-assist {
        color: #4477FF;
      }
      &-main {
        border-bottom-right-radius: 4px;
        background-image: linear-gradient(90deg, #584EED 7%, #5184FF 96%);
        color: #fff;
      }
    }
  }
}

.insurance-calendar {
  .header {
    display: flex;
    background: #fff;
    justify-content: center;
    align-items: center;
    height: 50px;
    font-size: 14px;
    margin-bottom: 12px;
    .left,
    .right {
      flex: 1;
      text-align: center;
      height: 100%;
      line-height: 50px;
    }
    .center {
      flex: 5;
      text-align: center;
      .title {
        font-size: 18px;
      }
      .sub-title {
        font-size: 12px;
        color: $font-color-light;
      }
    }
  }
  .top {
    background: #fff;
    border-bottom: 1px solid #DDDDDD;
    .date {
      padding: 17px 12px;
      font-size: 20px;
      color: #333;
      font-weight: bold;
    }
    .week-list {
      display: flex;
      .item {
        flex: 1;
        color: #333;
        font-size: 12px;
        padding: 10px;
        text-align: center;
        &-grey {
          color: $font-color-light;
        }
      }
    }
  }
  .block{
    margin-bottom: 12px;
  }
  .day-list {
    background: #fff;
    overflow: hidden;
    .item {
      position: relative;
      float: left;
      width: 14.28%;
      padding: 0;
      text-align: center;
      font-size: 0;
      .wrapper {
        padding: 6px;
      }
      .outer {
        position: relative;
        display: inline-block;
        width: 100%;
        padding-top: 100%;
        font-size: 0;
        border-radius: 50%;
        z-index: 1;
        .txt {
          position: absolute;
          width: 100%;
          padding-top: 100%;
          top: 0;
          left: 0;
          color: #333;
          font-size: 0;
          z-index: 9999;
          span {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 18px;
            color: #fff;
            color: #333;
          }
        }
      }
      &.disabled {
        .outer {
          .txt {
            span {
              color: $font-color-light;
            }
          }
        }
      }
      &.focus {
        .outer {
          background: #4477FF;
          font-weight: bold;
          span {
            color: #fff;
          }
        }
        .layout {
          position: absolute;
          top: 6px;
          bottom: 6px;
          left: 50%;
          right: 0;
          background: #8CACFF;
          z-index: 0;
        }
      }
      &.during {
        .outer {
          span {
            color: #fff;
          }
        }
        .layout {
          position: absolute;
          top: 6px;
          bottom: 6px;
          left: 0;
          right: 0;
          background: #8CACFF;
          z-index: 0;
        }
      }
      &.end {
        .outer {
          span {
            color: #fff;
          }
        }
        .layout {
          position: absolute;
          top: 6px;
          bottom: 6px;
          left: 0;
          right: 6px;
          border-top-right-radius: 46%;
          border-bottom-right-radius: 46%;
          background: #8CACFF;
          z-index: 0;
        }
      }
    }
  }
}