@charset "utf-8";
.org-choose-employee {
  .container {
    padding-top: 96px;
    padding-bottom: 45px;
    background: #fff;
    border-top: 1px solid #ccc;
  }
  .search-list {
    li {
      padding-right: 12px;
      .dept {
        color: #ccc;
      }
    }
  }
  .show-tap {
    .list {
      padding: 13px 13px 60px 13px;
      overflow: hidden;
      li {
        float: left;
        width: 23%;
        border: 1px solid #4477ff;
        color: #4477ff;
        text-align: center;
        font-size: 14px;
        padding: 6px 0;
        border-radius: 4px;
        margin-bottom: 10px;
        &:not(:nth-child(4n)) {
          margin-right: 2.6%;
        }
      }
    }
  }
}

.org-edit {
  padding-top: 44px;
  .search-list {
    background: #fff;
    margin-top: 10px;
    li {
      position: relative;
      i,
      span {
        vertical-align: middle;
      }
      .icon-arrow {
        position: absolute;
        top: 17px;
        right: 15px;
        @include arrow(right, 12px, 12px);
      }
    }
    &-pro {
      margin-top: 0;
      li {
        padding-left: 40px;
        .icon-delete {
          position: absolute;
          top: 50%;
          left: 10px;
          transform: translateY(-50%);
        }
        .icon-org-edit {
          position: absolute;
          top: 50%;
          right: 12px;
          transform: translateY(-50%);
        }
        .tel {
          color: #999;
        }
        &.del {
          .icon-delete {
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%) rotate(90deg);
          }
          .del-box {
            position: absolute;
            right: 0;
            top: 0;
            width: 60px;
            height: 100%;
            background: #FF0000;
            color: #fff;
            font-size: 14px;
            span {
              display: inline-block;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
            }
          }
        }
      }
    }
  }
  .operate-box {
    overflow: hidden;
    padding: 0 10px;
    font-size: 12px;
    margin-bottom: 10px;
    .select-btn {
      position: relative;
      padding: 7px;
      width: 67%;
      margin-right: 3%;
      float: left;
      background: #fff;
      border-radius: 4px;
      height: 32px;
      .icon-arrow {
        position: absolute;
        right: 7px;
        @include arrow(down, 10px, 10px);
      }
    }
    .back {
      padding: 7px;
      width: 30%;
      float: left;
      color: #4477ff;
      background: #fff;
      text-align: center;
      border-radius: 4px;
      &-disabled {
        color: #fff;
        background: #d8d8d8;
        border-radius: 4px;
      }
      &-edit{
        border: 1px solid #4477ff;
      }
    }
    &.open {
      margin-bottom: 0;
      .select-btn {
        height: 42px;
        .icon-arrow {
          top: 12px;
          @include arrow(up, 10px, 10px)
        }
      }
    }
  }
  .fix-bottom-bar {
    .plus {
      font-size: 22px;
    }
  }
  .crumbs-wrapper {
    padding: 10px 0;
    background: #fff;
  }
  .crumbs {
    overflow: hidden;
    height: 30px;
    .wrapper {
      width: 100%;
      overflow-x: scroll;
      padding-bottom: 10px; 
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .scroll-box {
      height: 30px;
      overflow: hidden;
      .item {
        float: right;
        width: 105px;
        height: 30px;
        line-height: 30px;
        border-radius: 15px;
        background: #fff;
        padding: 0 20px 0 20px;
        text-align: right;
        margin-right: -24px;
        font-size: 12px;
        border: 1px solid #ddd;
        overflow: hidden;
        color: #ccc;
        box-shadow: 0 0 3px rgba(#000000, 0.2);
        span {
          display: inline-block;
          width: 100%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        &:first-child {
          margin-right: 10px;
        }
        &.active {
          background: #4477ff;
          color: #fff;
          border: 1px solid #4477ff;
        }
      }
    }
  }
  .add-info {
    margin-top: 10px;
    span,
    i {
      vertical-align: middle;
    }
  }
}