.checkbox {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 1px solid #4175FE;
  border-radius: 3px;
  vertical-align: middle;
  &.actived {
    &::before {
      position: absolute;
      z-index: 3;
      top: 0;
      right: -1px;
      width: 12px;
      height: 5px;
      border-color: #4175FD;
      border-width: 0 0 1px 1px;
      border-style: solid;
      transform: rotateZ(-45deg);
      content: "";
    }
    &::after {
      position: absolute;
      ;
      top: -1px;
      right: -1px;
      width: 3px;
      height: 5px;
      background-color: #fff;
      content: "";
    }
  }
}

.icon-success-xlg {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 48px;
  background-color: #93CD23;
  border-radius: 50%;
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 10px;
    margin: -10px 0 0 -10px;
    border: 1px solid #fff;
    border-width: 0 0 2px 2px;
    content: "";
    transform: rotateZ(-45deg);
  }
}

