.corp-info {
  padding-top: 44px;
  font-size: 14px;
  .top {
    position: relative;
    padding: 20px 14px;
    background-image: linear-gradient(90deg, #5884F0 0%, #5D55CE 100%);
    color: #ffffff;
    .title {
      line-height: 14px;
      margin-bottom: 18px;
    }
    .money {
      line-height: 30px;
      .txt {
        margin-right: -5px;
      }
      .num {
        font-size: 26px;
      }
    }
    .tip {
      display: inline-block;
      margin-top: 8px;
      font-size: 12px;
    }
    .btn {
      position: absolute;
      right: 14px;
      bottom: 28px;
      line-height: 14px;
      display: inline-block;
      padding: 5px 12px;
      border: 1px solid #fff;
      color: #fff;
      background: rgba(#eff3fd, 0.1);
      border-radius: 3px;
      font-size: 12px;
      &-dtl {
        right: 95px;
      }
    }
  }
  .detail {
    display: flex;
    background: #fff;
    border: 1px solid $border-main-color;
    .content {
      position: relative;
      flex: 1;
      padding: 12px;
      .title {
        font-size: 14px;
        line-height: 14px;
        margin-bottom: 8px;
      }
      .money {
        &-orange {
          color: #ff7200;
        }
        &-blue {
          color: #698cec;
        }
        .txt {
          margin-right: -5px;
        }
        .num {
          font-size: 16px;
        }
      }
      .dtl {
        position: absolute;
        top: 10px;
        right: 12px;
        color: #6589ec;
      }
    }
  }
  .info {
    padding: 13px 15px;
    border-bottom: 1px solid $border-main-color;
    background: #fff;
    .title {
      display: inline-block;
      margin-top: 3px;
    }
    .money {
      float: right;
      &-orange {
        color: #ff7200;
      }
      .txt {
        font-size: 12px;
      }
      .num {
        font-size: 16px;
      }
    }
    .dtl {
      margin-top: 5px;
      float: right;
      color: #6589ec;
      font-size: 12px;
    }
  }
  .pay-way {
    padding: 15px 16px;
    background: #fff;
    margin-bottom: 10px;
    p {
      position: relative;
      padding-left: 29px;
      color: $font-color-gray;
      .state {
        float: right;
        color: $font-color-normal;
      }
      .operate{
        float: right;
        border: 1px solid #6589ec;
        color: #6589ec;
        font-size: 12px;
        line-height: 12px;
        padding:2px 5px;
        border-radius: 2px;
        margin-top: 1px;
        margin-left: 5px;
      }
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 27px;
        height: 23px;
        transform: translateY(-50%);
        background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/corp-info-icon.png');
        background-size: 105px 73px;
        background-position: -39px -5px;
      }
    }
  }
  .block {
    background: #fff;
    .header {
      padding: 10px 15px;
      border-bottom: 1px solid $border-main-color;
      p {
        padding-left: 8px;
        border-left: 3px solid #6286e9;
        font-size: 14px;
        line-height: 14px;
        color: $font-color-gray;
      }
    }
    .list {
      padding: 15px 0;
      display: flex;
      .item {
        position: relative;
        flex: 1;
        padding-top: 35px;
        text-align: center;
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 50%;
          transform: translateX(-50%);
          background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/corp-info-icon.png');
          background-size: 105px 73px;
        }
        &:first-child::before {
          width: 24px;
          height: 24px;
          background-position: -76px -5px;
        }
        &:nth-child(2)::before {
          width: 24px;
          height: 18.5px;
          background-position: -39px -46px;
        }
        &:nth-child(3)::before {
          width: 24px;
          height: 31px;
          background-position: -5px -5px;
        }
        &:nth-child(4)::before {
          width: 24px;
          height: 22px;
          background-position: -5px -46px;
        }
      }
    }
  }
  .msgbox-content-message{
    p{
      text-align: left;
      margin-bottom: 10px;
    }
  }
}

.fqa {
  .navbar {
    margin-top: 2px;
    display: -webkit-box;
    background: #fff;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: smooth;
    border-bottom: none;
    &::-webkit-scrollbar {
      display: none;
    }
    .nav-item {
      padding: 0 32px;
      height: 44px;
      line-height: 42px;
    }
  }
  .list {
    margin-top: 90px;
    padding: 10px;
    .item {
      background: #fff;
      padding: 13px;
      margin-bottom: 10px;
      border-radius: 4px;
      h3 {
        font-size: 14px;
        line-height: 14px;
        margin-bottom: 4px;
        color: $font-color-normal;
      }
      p {
        font-size: 12px;
        line-height: 18px;
        color: $font-color-light;
      }
    }
  }
}

.consumer {
  padding-top: 44px;
  background: #fff;
  .list {
    padding-left: 61px;
    .item {
      position: relative;
      padding: 15px 9px 15px 0;
      font-size: 12px;
      color: #333;
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        height: 1px;
        width: 100%;
        background: #d8d8d8;
        transform: scaleY(0.5);
      }
      .icon-consumer {
        position: absolute;
        left: -50px;
        top: 50%;
        transform: translateY(-50%);
      }
      .title {
        .txt {
          font-size: 14px;
        }
        .date {
          float: right;
          color: #999;
        }
      }
      .price {
        position: absolute;
        bottom: 13px;
        right: 9px;
        font-size: 16px;
        font-weight: bold;
        &-plus {
          color: #ff6600;
        }
      }
      &-limit {
        padding: 27px 9px 27px 0;
        .title {
          .txt {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
          }
          .date {
            position: absolute;
            right: 9px;
            top: 16px;
          }
        }
      }
    }
  }
  .upload {
    padding: 24px 0;
    text-align: center;
    color: $font-color-light;
    font-size: 14px;
  }
}