@charset "utf-8";
.flow {
  height: 100%;
  min-height: 100%;
  background: url('//pic.c-ctrip.com/corp_niv/me/platform/flow_bg.png') no-repeat 0/cover;
  .header {
    padding: 35px 0 18px 0;
    img {
      display: block;
      width: 50%;
      height: auto;
      margin: auto;
    }
  }
  .container {
    width: 100%;
    padding: 0 20px;
    @include clearfix;
    background: #fff;
    .card {
      margin-bottom: 25px;
      overflow: hidden;
      .item {
        width: 47%;
        padding: 90px 18px 30px 18px;
        float: left;
        border: 1px solid #b8cde6;
        border-radius: 4px;
        background: #fff;
        &:first-child {
          margin-right: 6%;
        }
        &-book {
          .title {
            color: #5d56cf;
            &::before {
              content: '';
              position: absolute;
              top: -58px;
              left: 50%;
              width: 49.5px;
              height: 51px;
              transform: translateX(-50%);
              background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/flow_icon.png');
              background-size: 120px 61px;
              background-position: -65.5px -5px;
            }
          }
          .btn {
            background: -webkit-linear-gradient(#5884f0, #5d55ce);
            background: -o-linear-gradient(#5884f0, #5d55ce);
            background: -moz-linear-gradient(#5884f0, #5d55ce);
            background: linear-gradient(#5884f0, #5d55ce);
          }
        }
        &-setting {
          .title {
            color: #007dd2;
            &::before {
              content: '';
              position: absolute;
              top: -58px;
              left: 50%;
              width: 50.5px;
              height: 51px;
              transform: translateX(-50%);
              background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/flow_icon.png');
              background-size: 120px 61px;
              background-position: -5px -5px;
            }
          }
          .btn {
            background: #007dd2;
          }
        }
        .title {
          position: relative;
          font-size: 14px;
          font-weight: bold;
          margin-bottom: 18px;
          text-align: center;
        }
        .des {
          font-size: 14px;
          color: #333;
          text-align: center;
          margin-bottom: 17px;
        }
        .btn {
          display: inline-block;
          position: relative;
          height: 30px;
          width: 100%;
          font-size: 16px;
          line-height: 29px;
          color: #fff;
          border-radius: 15px;
          text-align: center;
        }
        .icon {
          display: inline-block;
          &-flow-arrow {
            position: absolute;
            right: 18%;
            top: 50%;
            width: 8px;
            height: 8px;
            border-top: 1px solid #fff;
            border-right: 1px solid #fff;
            transform: translateY(-50%) rotate(45deg);
          }
        }
      }
    }
    .element {
      border-top: 1px solid #e6eef5;
      color: #007dd2;
      .block {
        overflow: hidden;
        display: flex;
        .item {
          position: relative;
          flex: 1;
          padding-top: 55px;
          text-align: center;
          font-weight: bold;
          font-size: 14px;
          &::before {
            content: '';
            position: absolute;
            top: 24px;
            left: 50%;
            transform: translateX(-50%);
            background-image: url('//pic.c-ctrip.com/corp_niv/me/platform/css_sprites_flow.png');
            background-size: 95px 72px;
            background-repeat: no-repeat;
          }
        }
        &-one {
          padding: 0 5%;
          .item {
            &:first-child::before {
              width: 25px;
              height: 25px;
              background-position: -5px -5px;
            }
            &:nth-child(2)::before {
              width: 22px;
              height: 18px;
              background-position: -68px -5px;
            }
            &:nth-child(3)::before {
              width: 18px;
              height: 24px;
              background-position: -40px -5px;
            }
          }
        }
        &-two {
          padding: 0 15%;
          .item {
            &:first-child::before {
              width: 22px;
              height: 27px;
              top: 24px;
              background-position: -5px -40px;
            }
            &:nth-child(2)::before {
              width: 12.5px;
              height: 19.5px;
              top: 32px;
              background-position: -68px -33px;
            }
          }
        }
      }
    }
  }
}