// 员工管理
.staff-detail {
  padding-top: 54px;
  .field-panel {
    padding: 0 12px;
    .field-grid {
      padding: 0 10px;
    }
  }
  .staff-hd {
    position: relative;
    padding-top: 10px;
    background-color: #fff;
    border-radius: 3px 3px 0 0;
  }
  .staff-label {
    display: inline-block;
    width: 80px;
    height: 30px;
    line-height: 30px;
    padding-left: 10px;
    color: #fff;
    background-color: #4377FF;
    border-radius: 0 15px 15px 0;
  }
  .btn-edit-staff {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}

.staff-add {
  padding-top: 88px;
  .field-panel {
    .field-row-multiple .inner-row .wrapper {
      padding: 0 50px 0 30px;
      input {
        border: 0;
        width: 100%;
        font-size: 14px;
      }
      &-operate {
        padding: 10px 0;
        position: relative;
        .arr-right-grid {
          right: 0;
        }
        &:not(:last-child) {
          border-bottom: 1px solid #eee;
        }
      }
    }
    .form-textarea {
      width: 96%;
    }
  }
  .col-4-1 {
    .arr-right-grid {
      right: 30px;
    }
  }
  .vcode {
    position: absolute;
    top: 0;
    right: 10px;
    width: 100px;
    height: 24px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    background-color: #4576FF;
    border-radius: 3px;
    &.disable {
      background-color: #999;
    }
  }
  .switch-lang {
    position: absolute;
    top: 0;
    right: 0;
    display: -webkit-flex;
    width: 70px;
    height: 24px;
    border-radius: 3px;
    overflow: hidden;
    .switch-item {
      -webkit-flex: 1;
      height: 24px;
      line-height: 24px;
      color: #fff;
      font-size: 12px;
      text-align: center;
      background-color: #ddd;
      &.cur {
        background-color: #4576FF;
      }
    }
  }
  .passport-wrap {
    .name-item {
      padding-right: 90px;
    }
    .lastname {
      padding-bottom: 10px;
    }
    .firstname {
      padding-top: 10px;
      border-top: 1px solid #eee;
    }
    &.cn {
      .lastname {
        padding-bottom: 0;
      }
      .firstname {
        display: none;
      }
    }
  }
}


.navbar .nav-item {
  &.cur {
    color: #4175FD;
    border-bottom: 2px solid #4175FD;
  }
}

.bind-phone .bind-result {
  padding: 20px 0 30px;
}

// 员工列表
.staff-manage {
  padding-bottom: 60px;
  .nav-item {
    &.cur {
      border-bottom: 2px solid #4175FD;
      color: #4175FD;
    }
  }
  .mg-wrapper {
    margin-top: 86px;
  }
  .search-box {
    background: #f0f0f0;
    padding: 12px;
    .wrapper {
      position: relative;
      padding: 5px 22px 5px 27px;
      border-radius: 4px;
      background: #fff;
      padding-left: 33px;
      .icon-payment-search-big {
        position: absolute;
        left: 9px;
        top: 50%;
        transform: translateY(-50%);
      }
      input {
        border: 0;
        background-color: transparent;
        width: 100%;
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
  .staff-content {
    background-color: #fff;
  }
  .select-wrap {
    padding: 10px 20px;
    font-size: 14px;
    border-bottom: 1px solid #F0F0F0;
    @include clearfix;
    .label-select-all {
      float: left;
      margin-left: -10px;
      .checkbox {
        margin-right: 5px;
      }
    }
    .btn-select,
    .btn-select-all,
    .btn-select-cancel {
      color: #4175FE;
    }
    .btn-select-cancel {
      float: right;
    }
  }
  .fix-bottom-bar {
    .plus {
      font-size: 20px;
      margin-right: 4px;
    }
  }
}

.staff-list {
  padding-left: 20px;
  overflow: hidden;
  &>li {
    position: relative;
    border-bottom: 1px solid #D8D8D8;
    overflow: hidden;
    &:last-child {
      border-bottom: 0;
    }
  }
  .staff-wrap {
    position: relative;
    z-index: 2;
    height: 54px;
    padding: 8px 0;
    background-color: #FFFFFF;
    transition: transform 300ms ease 100ms;
    &.slip {
      transform: translateX(-54px);
    }
    .checkbox-wrap {
      float: left;
      padding: 10px;
      margin-left: -20px;
      display: none;
    }
  }
  .name {
    font-size: 14px;
  }
  .email,
  .phone {
    color: #999;
  }
  .btn-staff-delete {
    position: absolute;
    top: 0;
    right: 0;
    width: 54px;
    height: 54px;
    line-height: 54px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    background-color: #FE0000;
  }
  .handle-wrap {
    position: absolute;
    z-index: 3;
    top: 10px;
    right: 10px;
  }
  .icon-staff-user,
  .icon-staff-cash {
    margin-left: 3px;
  }
}

.btn-staff-passed,
.btn-staff-refuse {
  display: inline-block;
  width: 54px;
  height: 34px;
  line-height: 34px;
  font-size: 14px;
  text-align: center;
  border-style: solid;
  border-width: 1px;
  border-radius: 3px;
}

.btn-staff-passed {
  color: #fff;
  background-color: #4175FD;
  border-color: #4175FD;
}

.btn-staff-refuse {
  color: #4175FD;
  background-color: #fff;
  border-color: #ccc;
}

.fix-bottom-staff {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px;
  background-color: #fff;
  overflow: hidden;
  display: none;
  .btn-staff-refuse {
    float: right;
  }
}

// 提交
.staff-submit {
  padding: 70px 0;
  text-align: center;
  .message {
    margin: 30px 0;
    font-size: 18px;
  }
  .btn-simple {
    display: inline-block;
    width: 130px;
    height: 30px;
    line-height: 30px;
    color: #4175FD;
    font-size: 16px;
    text-align: center;
    ;
    border: 1px solid #4175FD;
    border-radius: 3px;
  }
}

// 角色配置
.staff-role {
  padding-bottom: 60px;
  .role-list {
    background-color: #fff;
    &>li {
      position: relative;
      height: 58px;
      padding-left: 40px;
      &+li {
        border-top: 1px solid #F0F0F0;
      }
    }
    .checkbox-wrap {
      float: left;
      width: 40px;
      height: 58px;
      line-height: 58px;
      margin-left: -40px;
      text-align: center;
    }
    .role-name {
      padding-top: 10px;
      font-size: 16px;
    }
    .role-summary {
      color: #999;
    }
  }
  .help-tip {
    margin-top: 10px;
    padding-left: 40px;
    color: #999;
    .tag {
      float: left;
      width: 30px;
      margin-left: -30px;
      text-align: right;
    }
  }
}

//stuff-none
.staff-none {
  padding: 120px 0;
  .submit-info {
    text-align: center;
  }
  .message {
    font-size: 14px;
    color: #999999;
    margin-top: 20px;
  }
}

//choose stuff
.edit-staff {
  padding-top: 94px;
  padding-bottom: 54px;
  .field-panel {
    padding-top: 0;
  }
}

.edit-vip {
  padding-top: 60px;
  .staff-list {
    li {
      &:first-child {
        padding: 10px 0;
        text-align: center;
        font-size: 14px;
        i {
          width: 14px;
          height: 14px;
          border: 1px solid #06c;
          border-radius: 50%;
          display: inline-block;
          vertical-align: middle;
          position: relative;
          margin-right: 5px;
          &:after {
            position: absolute;
            content: "";
            background: #06c;
            width: 8px;
            height: 1px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
          &:before {
            position: absolute;
            content: "";
            background: #06c;
            width: 1px;
            height: 8px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
}

//staff
.edit-x {
  .staff-list {
    padding-left: 0;
    >li {
      border-bottom: 0 none;
    }
  }
  .field-panel .field-row {
    padding: 0 15px 0 0;
    line-height: 54px;
  }
}

.staff-department {
  .icon-checked {
    float: right;
  }
  .hide-tap {
    .active {
      color: #4175FD;
    }
    .btn-disable {
      background: #ccc !important;
    }
  }
}

.external-list {
  .mt10 {
    margin-top: 10px;
  }
  .tab-content {
    padding-top: 87px;
  }
  .fixed-input {
    position: relative;
    top: auto;
  }
  .search-list {
    background: #fff;
    ul {
      li {
        position: relative;
        .note {
          margin-top: 4px;
          font-size: 12px;
          color: #999;
          line-height: 12px;
        }
        .tel {
          position: absolute;
          font-size: 12px;
          right: 12px;
          top: 12px;
        }
        i,
        span {
          vertical-align: middle;
        }
        .icon-arrow {
          position: absolute;
          top: 17px;
          right: 15px;
          @include arrow(right, 12px, 12px);
        }
      }
    }
  }
  .msgbox-content {
    .input-box {
      padding: 0 10%;
      .title {
        font-size: 14px;
        margin-bottom: 10px;
      }
      input {
        width: 100%;
        border: 1px solid #b1b1b1;
        padding: 5px;
        font-size: 14px;
      }
    }
  }
}

.external-group-list {
  padding-top: 54px;
  .search-list {
    background: #fff;
    margin-top: 10px;
    ul {
      li {
        position: relative;
        .note {
          margin-top: 4px;
          font-size: 12px;
          color: #999;
          line-height: 12px;
        }
        .tel {
          position: absolute;
          font-size: 12px;
          right: 12px;
          top: 12px;
        }
      }
    }
  }
}

.fqt-tourist-list {
  margin-top: 94px;
  background: #fff;
  ul {
    li {
      position: relative;
      padding-left: 30px;
      padding-right: 30%;
      .icon-org-edit {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
      .field-checkbox {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
      }
      .warning {
        position: absolute;
        right: 37px;
        top: 50%;
        transform: translateY(-50%);
        color: red;
      }
      .note {
        margin-top: 4px;
        font-size: 12px;
        color: #999;
        line-height: 12px;
      }
      .tel {
        position: absolute;
        font-size: 12px;
        right: 12px;
        top: 12px;
      }
    }
  }
  .show-tap {
    .list {
      padding-left: 10px;
      padding-bottom: 50px;
      ul {
        .item {
          font-size: 14px;
          padding: 15px 12px 15px 0;
          border-bottom: 1px solid #dbdbdb;
        }
      }
    }
  }
}

.import-state {
  .result {
    padding: 0 10px;
    position: relative;
    padding-left: 30px;
    font-size: 14px;
    margin-bottom: 10px;
    .icon-payment {
      position: absolute;
      left: 10px;
      top: 2px;
    }
  }
  .card {
    padding:0 10px;
    .wrapper {
      position: relative;
      background: #fff;
      border-radius: 4px;
      margin-bottom: 10px;
    }
    .num {
      position: absolute;
      top: 10px;
      left: 10px;
      display: inline-block;
      width: 18px;
      height: 18px;
      line-height: 18px;
      border-radius: 50%;
      background: #6699ff;
      text-align: center;
      color: #fff;
    }
    .content {
      padding: 10px 10px 10px 36px;
    }
    .row {
      &:not(:last-of-type) {
        margin-bottom: 5px;
      }
      .tit {
        display: inline-block;
        width: 60px;
        color: #999;
        text-align: right;
      }
    }
  }
}